
  /* end  medicx pricing section */
//  medicx pricing section v4

.medicx-pricing{
  &-wrapper {
    display: flex;
    @include max (991px){
      display: block;
    }
  }
  &-wrap{
    border-radius: 20px 0 0 20px;
    padding: 30px;
    margin-bottom: 24px;
    transition: all 0.4s;
    background-color:  var(--white-bg);
    box-shadow: 0px 4px 80px 0px #00000014;
    &.active{
      width: 530px;
      border-radius: 20px;
      background: var(--heading-color);
      @include max (991px){
        width: 100%;
      }
    }
  }
  &-header{
    margin-bottom: 15px;
    img{
        margin-bottom: 30px;
    }
  }
  &-price{
    gap: 10px;
    display: flex;
    align-items: flex-end;
    padding-bottom: 24px;
    margin-bottom: 24px;
    border-bottom: 1px solid var( --light-color2);
    .medicx-price-value{
      font-weight: 700;
      color: var(--heading-color);
      font-size: 70px;
      line-height: 80px;
    }
  }
  &-body{
    ul{
        li{
            position: relative;
            font-weight: 700;
            padding-left: 40px;
            margin-bottom: 15px;
            color: var(--heading-color);
            &:last-child{
                margin-bottom: 0;
            }
            img{
                position: absolute;
                left: 0;
                top: 5px;
            }
        }
    }
  }
  &-bodyv4 {
    margin-bottom: 0!important;
  }
}



