.medicx-footer-top {
    padding: 130px 0 90px;
    @include max (991px){
        padding: 100px 0 70px;
    }
    @include max (767px){
        padding: 70px 0 50px;
    }
  }

  .medicx-footer-wrap{
    @include max(1199px) {
        max-width: 500px;
    }
    a{
        img.footer1-img {
            margin-bottom: 24px;
        }
    }
    
    p {
        margin-bottom: 24px;
        font-size: 18px!important;
    }
    &.light-color {
        color: var(--white-color);
    }
    &.ml-60{
        @include max (1199px) {
            margin-left: 0;
            max-width: 600px;
        }
    }
  }
.footer1-contact-data ul li {
    position: relative;
    font-size: 19px;
    font-weight: 400;
    line-height: 30px;
    text-align: left;
    color: var(--white-color);
    margin-bottom: 15px;
    img{
        position: absolute;
        top: 5px;
        left: -35px;

    }
}
.medicx-footer-help-content{
    ul {
        li{
            display: inline-block;
            margin-left: 40px;
        }
    }
}
 .mr-15{
    margin-right: 15px;
    @include max(991px) {
        margin-right: 0;
    }
 }
.ml-15{
    margin-left: 15px;
    @include max(991px) {
        margin-left: 0;
    }
}
.ml-60{
    margin-left: 60px;
    @include max(991px) {
        margin-left: 0;
    }
}
.fs-22{
    h5{
        font-size: 22px!important;
    }
}
 .mr-25{
    margin-right: 25px;
    @include max(991px) {
        margin-right: 0;
    }
 }
 .ml-40{
    margin-left: 40px;
    @include max(991px) {
        margin-left: 0;
    }
 }
.mt-40{
    margin-top: 40px;
    @include max(991px) {
        margin-top: 30px;   
    }
 }
  .medicx-footer-menu {
    h5 {
        margin-bottom: 24px;
    }
    ul {
        li{
            margin-bottom: 15px;
            &:last-child{
                margin-bottom: 0;
            }
            a{
                color: var(--body-color);
                position: relative;
                transition: all 0.4s;
            }
        }
    }
    @include max (1199px){
        margin-top: 30px;
    }
    &.light-color{
        h5{
            color: var(--white-color);
        }
        ul {
            li{
                a{
                    color: var(--white-color);
                }
            }
        }
    }
  }

.medicx-footer-menu.light-color ul li a::before {
    background: var(--white-color);
}

.medicx-footer-menu ul li a:hover:before {
    width: 100%;
}
.medicx-footer-menu ul li a:before {
    content: " ";
    left: 0;
    bottom: 0;
    width: 0;
    height: 1px;
    position: absolute;
    transition: all .4s;
    background: var(--heading-color);
}




.medicx-footer3 {
    &-wrap {
        display: flex;
        justify-content: space-between;
        padding: 30px 0;
        border-top: 1px solid var(--body-color);
        @include max (767px){
            display: block;
            text-align: center;
        }
    }
    &-text{
        align-items: center;
        display: flex;
        @include max (767px){
            display: block;
            margin: 0 0 30px 0;
        }
    }
    p {
        line-height: 28px;
        color: var(--grey-color);
    }
}
// end medicx footer 1
.medicx-subscription-field2  {
    input{
       margin: 0;
       height: 50px;
       width: 100%;
       color: var(--white-color);
       font-weight: 600;
       font-size: 16px;
       border-radius: 50px;
       background-color: var(--heading-color)!important;
       padding: 15px 28px !important;
       transition: all .4s;
       letter-spacing: .5px;
       border: 1px solid var(--body-color) !important;
       &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
          color: var(--white-color);
        }
        &::-moz-placeholder { /* Firefox 19+ */
          color: var(--white-color);
        }
        &:-ms-input-placeholder { /* IE 10+ */
          color: var(--white-color);
        }
        &:-moz-placeholder { /* Firefox 18- */
          color: var(--white-color);
        }
        &:focus{
            border: 1px solid var(--white-color)!important;
        }
    }
    
  }