header.medicx-header-section {
  position: fixed;
  z-index: 9;
  width: 100%;
  top: 0;
  transition: all 0.4s;
  background: var(--white-color);
}
header.medicx-header-section.sticky-menu{
  box-shadow: 0 4px 80px rgb(0 0 0 / 6%);
  background-color: var(--white-bg);
}

.medicx-header-top, 
.medicx-header-top2, 
.medicx-header-top3{
  transition: all 0.4s;
}
.sticky-menu{
  .medicx-header-top{
    margin-top: -60px;
  }
  .medicx-header-top2{
    margin-top: -84px;
  }
  .medicx-header-top3{
    margin-top: -81px;
  }
}

.white-color{
  color: var(--white-color)!important;
}

header.medicx-header-section.sticky-menu.inner-header{

  background-color: var(--dark-bg)!important;
}
.inner-header .site-menu-main .nav-link-item {
  color: var(--white-color)!important;
  @include max(991px){
    color: var(--dark-bg)!important;
  }
}
// v9
.header-three-site-btn{
  align-items: center;
  gap: 12px;
  a.header-three-btn{
    color: var(--heading-color);
    font-size: 16px;
    line-height: 24px;
    font-weight: 700;
  }
}




// medicx project

.medicx-header{
  &-top{
    background: var(--accent-color);
    padding: 15px 0;
    @include max (991px){
      display: none;
    }
  }
  &-top2{
    background: var(--white-color);
    padding: 15px 0;
    position: relative;
    z-index: 1;
    @include max (991px){
      display: none;
    }
  }
  &-top3{
    background: var(--white-color);
    padding: 15px 0;
    position: relative;
    z-index: 1;
    @include max (991px){
      display: none;
    }
  }
  
}
.medicx-header{
  &-bottom {
    padding: 15px 0;
    background: var(--white-color);
  }
  &-bottom2 {
    padding: 15px 0;
    background: var(--accent-color);
  }
  &-bottom3 {
    padding: 15px 0;
    position: relative;
    z-index: 1;
    background: var(--heading-color);
  }
}
.medicx-header-info{
  &-wrap{
    display: flex;
    align-items: center;
    justify-content: space-between;
    @include max (1199px){
      display: block;
      text-align: center;
    }
  }
  &-wrap2{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &-data{
    ul{
      li{
        display: inline-block;
        margin-right: 20px;
        color: var(--white-color);
        img{
          margin-right: 10px;
        }
        @include max (767px){
          margin-bottom: 10px;
        }
        a{
          margin-right: 20px;
          color: var(--white-color);
         
          img{
            margin-right: 10px;
          }
        }
      }
    }
  }
  &-data3{
    ul{
      li{
        display: inline-block;
        margin-right: 20px;
        font-weight: 700;
        color: var(--heading-color);
        img{
          margin-right: 10px;
        }
        @include max (767px){
          margin-bottom: 10px;
        }
        a{
          font-weight: 700;
          margin-right: 20px;
          color: var(--heading-color);
         
          img{
            margin-right: 10px;
          }
        }
      }
    }
  }
  
}
.brand-logo2{
  display: none;
  @include max (991px){
    display: block;
  }
}

.brand-logo3{
  padding: 0 50px;
  top: -96px;
  left: -25%;
  display: flex;
  height: 171px;
  align-items: center;
  position: absolute;
  transition: all 0.4s;
  background: var(--accent-color);

  @include max (1700px){
    position: static;
    padding: 0 30px;
    height: 80px;
  }
  @include max (767px){
    padding: 0;
    background: transparent;
  }
}

.sticky-menu{
  .brand-logo3 {
    padding-top: 80px;
    @include max (1700px){
      padding-top: 0;
    }
  }
}
.medicx-social-icon{
  &-wrap{
    @include max (1199px){
      margin-top: 15px;
    }
  }
  &-img{
      ul {
          li{
              display: inline-block;
              @include max (767px){
                margin-bottom: 20px;
              }
              a{
                margin-right: 30px;
                
              }
          }
      }
  }
}



