.medicx-social-icon-wrap{
    .medicx-footer3{
        ul{
            li{
                a{
                    border: 1px solid var(--heading-color);
                    &:hover{
                        border: 1px solid var(--accent-color);
                        svg path{
                            fill: white;
                        }
                    }
                }
            }
        }
    }
}
.medicx-footer-social{
    &-wrap {
        display: flex;
        flex-wrap: wrap;
        padding-bottom: 80px;
        @include max (991px){
            padding-bottom: 60px;            
        }
        @include max (767px){
            padding-bottom: 40px;            
        }
    }
    &-button-wrap{
        display: flex;
        gap: 10px;
        align-items: center;
        padding: 16px 78px;
        transition: all 0.4s;
        border: 1px solid var(--accent-color);
        @include max (1199px){
            margin-bottom: 24px;
            
        }
        &.br-1{
            border-radius: 50px 0 0 50px;
            @include max (1199px){
                border-radius: 0;
            }
        }
        &.br-2{
            border-radius: 0 50px 50px 0;
            @include max (1199px){
                border-radius: 0;
            }
        }
        &:hover{
            background: var(--accent-color);
        }
    }
    &-button-data{
        p{
            font-size: 16px;
            line-height: 28px;
            font-weight: 400;
            color: var(--white-color);
        }
    }
    &-button-icon {
        margin-bottom: 5px;
    }
}
.center{
    text-align: center;
}
.medicx-footer-bottom{
    display: block;
    padding: 35px 0;
    border-top: 1px solid var(--body-color);
    p{
        color: var(--white-color);
    }
}
