.medicx-about{
    &-wrapperv3 {
        padding: 40px;
        background: white;
        border-radius: 10px;
        display: block;
        position: relative;
        margin-bottom: 24px;
        .medicx-about-btn{
            svg{
                margin: 8px 0 0 10px;
            }
            &.about-position {
                transition: all .4s;
                position: absolute;
                bottom: 125px;
                left: 0;
                text-align: center;
                width: 100%;
                opacity: 0;
                visibility: hidden;
            }
        }
        &:hover .medicx-about-thumb::before {
            opacity: 1;
        }
        &:hover .about-position {
            opacity: 1;
            visibility: visible;
            bottom: 30%;
            left: 0%;
        }
    }
    &-titlev3{
        &-wrap{
            display: flex;
            gap: 15px;
            align-items: center;
            margin-bottom: 24px;
        }
        &-icon{}
    }
    &-thumb{
        position: relative;
        img{
            border-radius: 10px;
            width: 100%;
        }
        p{
            font-size: 20px!important;
        }
        &::before {
            position: absolute;
            content: "";
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            background: linear-gradient(180deg, rgba(3, 3, 3, 0) 0%, #030303 100%);
            opacity: 0;
            transition: .3s;
        }
    }
    &-data{
        p{
            margin-top: 24px;
        }
    }
}
