


.medicx-iconbox{
    &-wrap {
        border-radius: 10px;
        background-color: var(--white-color);
        padding: 30px;
        margin-bottom: 24px;
        transition: all 0.4s;
        &:hover{
            background: var(--accent-color);
            .medicx-iconbox-data{
                h4,p{
                    color: var(--white-color);
                }
            }
            .medicx-iconbox-btn{
                    color: var(--white-color);
                    svg path{
                        stroke: var(--white-color);
                    }
            }
            
            .medicx-iconbox-icon{
                background: var(--white-color);
            }
            svg path{
                fill: var(--accent-color);
            }
            svg.svg-hover{
                stroke: var(--accent-color-color);
            }
            svg.icon-stroke path{
                stroke: var(--accent-color);
                fill: #fff;
                opacity: 1;
            }
           
        }
    }
   
    &-icon {
        margin-bottom: 24px;
        width: 80px;
        height: 80px;
        display: flex;
        align-items: center;
        border-radius: 100px;
        justify-content: center;
        background: var(--accent-color);
        position: relative;
        transition: all .4s;
    }
    &-data {
        h4 {
            font-size: 34px;
            transition: all 0.4s;
            margin-bottom: 15px;
        }
        p{
            transition: all 0.4s;
            font-size: 18px;
        }
        
       
    }
    &-btn{
        color: var(--accent-color);
        font-weight: 700;
        svg{
            transition: all .4s;
            margin-left: 10px;
            path{
                transition: all 0.4s;
            }
        }
        
    }
    &-wrap2 {
        display: flex;
        gap: 12px;
        margin-bottom: 24px;
        text-align: left;
        align-items: center;
        &:last-child {
            margin-bottom: 0;
        }
    }
    &-data2{
        h4{
            margin-bottom: 15px;
        }
        h6{
            color: var(--heading-color);
            margin-bottom: 0!important;
        }
        &.max-width-360{
            max-width: 300px;
        }
    }
    &-icon2 {
        flex-shrink: 0;
        width: 60px;
        height: 60px;
        background: var(--accent-color);
        border-radius: 100px;
        img{
            margin: 20px 0 0 20px;
        }
    }
    &-data2 {
        h5 {
            margin-bottom: 5px;
        }
        a {
            color: var(--white-color);
            font-size: 20px;
            &:last-child{
                margin-bottom: 0;
            }
        }
    }
    &-wrap3 {
        display: flex;
        gap: 24px;
        text-align: left;
        margin-bottom: 24px;
        &:last-child {
            margin-bottom: 0;
        }
    }
    &-icon3 {
        flex-shrink: 0;
    }
    &-data3 {
        h5 {
            margin-bottom: 5px;
        }
    }
    &-wrapper4{
        margin-top: 90px;
    }
    &-wrap4{
        margin-bottom: 35px;
    }
}
.medicx-content{
    &-wrapv3{
        padding-bottom: 50px;
        border-bottom: 1px solid rgba(3, 3, 3, 0.1);
        @include max (991px){
            padding-bottom: 40px;
        }
        @include max (9767px){
            padding-bottom: 30px;
        }
    }
}

.medicx-iconbox{
    &-warpv3 {
        display: flex;
        gap: 10px;
    }
    &-authore{
        h5{
            margin-bottom: 15px;
        }
    }
    &-datav3{
        margin-bottom: 24px;
            &:last-child{
                margin-bottom: 0;
            }
    }
}
// aboutus inner page

.medicx-aboutus-content{
    margin-top: 80px;
    @include max (991px){
        margin-top: 60px;
    }
    @include max (767px){
        margin-top: 40px;
    }
    &-title{
        margin-top: 35px;
        @include max (767px){
            margin-top: 50px;
        }
        ul{
            &.nav-tabs {
                width: 850px;
                height: 6px;
                border-radius: 100px;
                display: flex;
                justify-content: space-between;
                border-bottom: 5px solid rgba(0, 34, 255, 0.05);
                margin: 0 auto;
                @include max (991px){
                    width: 732px;
                    justify-content: center;
                }
                @include max (767px){
                    width: 500px;
                    justify-content: center;
                    flex-wrap: nowrap;
                }
            }
            li{
                &.nav-item{
                    margin-top: -42px;
                    @include max (767px){
                        margin-top: -62px;
                    }  
                }
            }
            a{
                &.nav-link {
                    margin-bottom: -1px;
                    background: 0 0;
                    border: none;
                    border-top-left-radius: .25rem;
                    border-top-right-radius: .25rem;
                }
                &.nav-link.active{
                    border-bottom: 5px solid var(--accent-color);
                }
            }
        }
    }
}
.medicx-content-thumbb{
    text-align: center;
    img{
        width: 100%;
        display: inline-block;
    }
}