.servicev1{
    margin-top: -100px;
    @include max (991px){
        margin-top: -80px;
    }
    @include max (767px){
        margin-top: -60px;
    }
}
.medicx-service-slider-icon {
    animation: animName 3s linear infinite;
}


.medicx-content-slider{
    background: var(--accent-color);
    padding: 30px 0;
}
.medicx-service-slider-wrap{
    display: flex!important;
    gap: 35px;
    align-items: center;
}

// v2




.medicx-service{
    &-wrap{
        display: block;
        position: relative;
        .service_position {
            transition: all .4s;
            position: absolute;
            bottom: 0;
            left: 24px;
            width: 100%;
            opacity: 0;
            visibility: hidden;
            width: 415px;
            padding: 24px;
            border-radius: 10px;
            background-color: var(--accent-color);
            h4{
                margin-bottom: 15px;
            }
        }
        :hover .medicx-service-thumb::before {
            opacity: 1;
        }
        :hover .service_position {
            opacity: 1;
            visibility: visible;
            bottom: 24px;
            left: 24px;
        }
    }
    &-thumb{
        margin-bottom: 24px;
        position: relative;
        &::before {
            position: absolute;
            content: "";
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            opacity: 0;
            transition: .3s;
        }
    }
    &-content-wrap{
        a.medicx-site-btn{
            font-size: 16px;
            line-height: 28px;
            font-weight: 700;
            color: var(--white-color);
            img{
                margin-left: 10px;
            }
        }
    }
    
}
.mt-60{
    margin-top: 60px;
    @include max (991px){
        margin-top: 40px;
    }
}
.tacc{
    text-align: center;
}

// service details
.medicx-service-details{
    &-text{
        p{
            font-size: 20px;
            line-height: 32px;
        }
        ul{
            p{
                margin-top: 40px;
                @include max (767px){
                    margin-top: 30px;
                }
            }
            li{
                font-weight: 600;
                margin-bottom: 8px;
            }
        }
    }
    &-skill{
        h4{
            margin-bottom: 15px;
        }
        p{
            margin-bottom: 24px;
        }
        ul{
            li{
                font-weight: 600;
                font-size: 20px;
                color: var(--heading-color);
                margin-bottom: 5px;
                &:last-child{
                    margin-bottom: 0;
                }
            }
        }
    }
}